import { NgModule } from '@angular/core';
import { RouterModule,Routes } from '@angular/router';
import { HomeComponent } from './home/home.component';
import { DealsComponent } from './deals/deals.component';
import { SignUpComponent } from './sign-up/sign-up.component';
import { ReleasesComponent } from './releases/releases.component';
import { WomensShoesComponent } from './womens-shoes/womens-shoes.component';
import { WomensClothesComponent } from './womens-clothes/womens-clothes.component';
import { KidsShoesComponent } from './kids-shoes/kids-shoes.component';
import { KidsClothesComponent } from './kids-clothes/kids-clothes.component';
import { MensClothesComponent } from './mens-clothes/mens-clothes.component';

// routes
const routes:Routes = [
  {path:'', component:HomeComponent},
  {path:'deals', component:DealsComponent},
  {path:'signup', component:SignUpComponent},
  {path:'releases', component:ReleasesComponent},
  {path:'womens-shoes', component:WomensShoesComponent},
  {path:'womens-clothes', component:WomensClothesComponent},
  {path:'kids-shoes', component:KidsShoesComponent},
  {path:'kids-clothes', component:KidsClothesComponent},
  {path:'mens-clothes', component:MensClothesComponent}]
  
@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
